<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>Nier </v-card-title>
          <v-card-text>
            <v-file-input label="" v-model="file"></v-file-input>
            <v-row>
              <v-col>
                <v-checkbox v-model="debug" label="" />
              </v-col>
              <v-col>
                <v-checkbox v-model="english" label="" />
              </v-col>
              <v-col>
                <v-radio-group v-model="lang" row>
                  <v-radio label="" value="cn"></v-radio>
                  <v-radio label="" value="tw"></v-radio>
                  <v-radio label="" value="hk"></v-radio>
                </v-radio-group>
              </v-col>
            </v-row>

            <v-btn
              class="ml-2"
              color="success"
              :disabled="(file && file.length === 0) || !file"
              @click="saveProject"
            ></v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="alert" width="600px">
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>{{alertMessage}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="red" @click="alert = false; alertMessage ='';"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import FileSaver from "file-saver";

export default {
  data: () => ({
    loaded: false,
    alert: false,
    alertMessage: "",
    file: [],
    debug: false,
    english: false,
    lang: "cn"
  }),
  mounted() {
    this.$store.commit("setCanGoBack", true);
  },
  methods: {
    saveProject() {
      if (!this.file) {
        this.alertMessage = "";
        this.alert = true;
      } else {
        this.$api.export(this.file, {debug: this.debug, lang: this.lang, english: this.english}).then(res => {
          let data = res.data;
          if (typeof data === "object") {
            this.alertMessage = res.data.msg;
            this.alert = true;
          } else {
            const contentDisposition = res.headers["content-disposition"];
            let fileName = "output";
            if (contentDisposition) {
              const fileNameMatch = contentDisposition.match(/filename="(.+)"/);
              if (fileNameMatch.length === 2) fileName = fileNameMatch[1];
            }
            var blob = new Blob([data], { type: res.headers["content-type"] });
            FileSaver.saveAs(blob, fileName);
          }
        });
      }
    }
  }
};
</script>