<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card class="d-block">
          <v-tabs v-model="tab">
            <v-tab></v-tab>
            <v-tab v-if="loaded"></v-tab>
            <v-tab v-if="loaded"></v-tab>
            <v-tab v-if="loaded"></v-tab>
          </v-tabs>
          <v-tabs-items v-model="tab">
            <v-tab-item>
              <v-card flat>
                <v-card-text>
                  <input id="file-input" type="file" hidden />
                  <v-btn color="primary" @click="loadProject">{{
                    openText
                  }}</v-btn>
                  <v-btn
                    class="ml-2"
                    color="success"
                    :disabled="!loaded"
                    @click="saveProject"
                    ></v-btn
                  >
                  <template v-if="loaded">
                    <span class="ml-4"> {{ filename }}</span>
                    <span class="ml-2"> {{ project.spec }}</span>
                    <span class="ml-2"> {{ project.line }}</span>
                    <span class="ml-2"> {{ project.total }}</span>
                    <span class="ml-2"> {{ progress }}</span>
                  </template>
                  <template v-else>
                    <span class="ml-4"
                      > *.json</span
                    >
                  </template>
                </v-card-text>
              </v-card>
            </v-tab-item>
            <v-tab-item v-if="loaded">
              <v-card flat>
                <v-card-text>
                  <v-row>
                    <v-col>
                      <v-text-field
                        type="text"
                        v-model="jump"
                        label=""
                        placeholder="#1234"
                        @keyup="quickJump"
                      ></v-text-field>
                    </v-col>
                    <v-col>
                      <v-select
                        :items="sizePreset"
                        label=""
                        @change="sizeChange"
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-tab-item>
            <v-tab-item v-if="loaded">
              <v-card flat>
                <v-card-text>
                  <v-row>
                    <v-col>
                      <v-text-field
                        type="text"
                        v-model="searchWord"
                        label=""
                        placeholder
                        @keyup="search"
                      />
                    </v-col>
                    <v-col>
                      <v-text-field
                        type="text"
                        v-model="searchWordTrans"
                        label=""
                        placeholder
                        @keyup="searchTrans"
                      />
                    </v-col>
                    <v-col cols="2">
                      <v-switch
                        v-model="searchFuzzy"
                        label=""
                      ></v-switch>
                    </v-col>
                    <v-col>
                      <v-btn
                        class="ml-2 mt-2"
                        color="primary"
                        @click="clearSearch"
                        ></v-btn
                      >
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-tab-item>
            <v-tab-item v-if="loaded">
              <v-card flat>
                <v-card-text>
                  <v-row>
                    <v-col>
                      <v-switch
                        v-model="autoBackup"
                        label=""
                      ></v-switch>
                    </v-col>
                    <v-col>
                      <v-switch
                        v-model="avoidEmptyLine"
                        label=""
                      ></v-switch>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-if="loaded" id="work-row">
      <v-col>
        <v-card>
          <v-card-text>
            <v-pagination
              v-model="page"
              :length="pageLength"
              total-visible="10"
              circle
            ></v-pagination>
            <template>
              <div v-for="item in pageItem" :key="item.Id">
                <v-row no-gutters :id="'item-' + item.Id">
                  <v-col>
                    <span>#{{ item.Id }}</span>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="3">
                    <v-textarea
                      label=""
                      readonly
                      no-resize
                      v-model="item.JpText"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="3">
                    <v-textarea
                      label=""
                      readonly
                      no-resize
                      v-model="item.ChsText"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="3">
                    <v-textarea
                      label=""
                      readonly
                      no-resize
                      v-model="item.ChtText"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="3">
                    <v-textarea
                      label=""
                      v-model="item.Comment"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </div>
            </template>

            <v-pagination
              v-model="page"
              :length="pageLength"
              total-visible="10"
              circle
              @input="$vuetify.goTo('#work-row')"
            ></v-pagination>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="alert" width="600px">
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>{{ alertMessage }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            text
            color="red"
            @click="
              alert = false;
              alertMessage = '';
            "
            ></v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="backupNotify" :timeout="backupNotifyTimeout">
      {{ backupNotifyMessage }}
      <v-btn color="blue" text @click="backupNotify = false"></v-btn>
    </v-snackbar>
  </v-container>
</template>
<script>
import utils from "../utils/utils";
import FileSaver from "file-saver";
import dateFormat from "dateformat";
export default {
  data: () => ({
    loaded: false,
    filename: "",
    alert: false,
    alertMessage: "",
    backupNotify: false,
    backupNotifyMessage: "",
    backupNotifyTimeout: 2000,
    avoidEmptyLine: true,
    project: {},
    extraData: {},
    tab: 0,
    page: 1,
    sizePreset: [30, 50, 80, 100],
    size: 50,
    jump: 0,
    total: 0,
    translate: 0,
    autoBackup: true,
    autoBackupTimer: {},
    talkerNameMap: [
      { id: "1", name: "" },
      { id: "2", name: "" },
      { id: "3", name: "" },
      { id: "4", name: "" },
      { id: "5", name: "" },
      { id: "6", name: "" },
      { id: "7", name: "" },
      { id: "8", name: "" },
      { id: "9", name: "" },
      { id: "10", name: "" },
      { id: "11", name: "" },
      { id: "12", name: "" },
      { id: "13", name: "" },
      { id: "14", name: "" },
      { id: "15", name: "" },
      { id: "16", name: "" },
      { id: "17", name: "" },
      { id: "18", name: "" },
      { id: "19", name: "" },
      { id: "20", name: "" },
      { id: "21", name: "" },
      { id: "22", name: "" },
      { id: "23", name: "" },
      { id: "24", name: "" },
      { id: "25", name: "" },
      { id: "26", name: "" },
      { id: "27", name: "" },
      { id: "28", name: "" },
      { id: "29", name: "" },
      { id: "30", name: "" },
      { id: "31", name: "" },
      { id: "32", name: "" },
      { id: "33", name: "" },
      { id: "34", name: "" },
      { id: "35", name: "" },
      { id: "36", name: "" },
      { id: "37", name: "" },
      { id: "38", name: "" },
      { id: "39", name: "" },
      { id: "40", name: "" },
      { id: "41", name: "" },
      { id: "42", name: "" },
      { id: "43", name: "" },
      { id: "44", name: "" },
      { id: "45", name: "" },
      { id: "46", name: "" },
      { id: "47", name: "" },
      { id: "48", name: "" },
      { id: "49", name: "" },
      { id: "50", name: "" },
      { id: "51", name: "" },
      { id: "52", name: "" },
      { id: "53", name: "" },
      { id: "54", name: "" },
      { id: "55", name: "" },
      { id: "56", name: "" },
      { id: "57", name: "" },
      { id: "58", name: "" },
      { id: "59", name: "" },
      { id: "60", name: "" },
      { id: "61", name: "" },
      { id: "62", name: "" },
      { id: "63", name: "" },
      { id: "64", name: "" },
      { id: "65", name: "" },
      { id: "66", name: "" },
      { id: "67", name: "" },
      { id: "68", name: "" },
      { id: "69", name: "" },
      { id: "70", name: "" },
      { id: "71", name: "" },
      { id: "72", name: "" },
      { id: "73", name: "" },
      { id: "74", name: "" },
      { id: "108", name: "" },
      { id: "110", name: "" },
      { id: "111", name: "" },
      { id: "112", name: "" },
      { id: "113", name: "" },
      { id: "114", name: "" },
      { id: "115", name: "" },
      { id: "116", name: "" },
      { id: "118", name: "" },
      { id: "119", name: "" },
      { id: "120", name: "" },
      { id: "127", name: "" },
      { id: "129", name: "" },
      { id: "132", name: "" },
      { id: "146", name: "" },
      { id: "147", name: "" },
      { id: "149", name: "" },
      { id: "150", name: "" },
      { id: "151", name: "" },
      { id: "152", name: "" },
      { id: "171", name: "" },
      { id: "172", name: "" },
    ],
    searchWord: "",
    searchWordTrans: "",
    searchFlag: false,
    searchTransFlag: false,
    searchFuzzy: true,
  }),
  computed: {
    openText() {
      return this.loaded ? "" : "";
    },
    progress() {
      var translated = 0;
      this.project.list.forEach((element) => {
        "" !== element.Translated &&
          null !== element.Translated &&
          translated++;
      });
      return translated;
    },
    displayList() {
      var list = this.project.list;
      if (this.avoidEmptyLine) {
        if (this.project.spec === "INW") {
          list = list.filter((i) => i.Text + i.Text2 !== "");
        } else {
          list = list.filter((i) => i.Text !== "");
        }
      }
      if (this.searchFlag) {
        if (this.project.spec === "INW") {
          list = list.filter((i) => {
            if (this.searchFuzzy) {
              return this.searchWordReg(this.searchWord).test(
                i.Text.concat(i.Text2)
              );
            } else {
              return i.Text.concat(i.Text2).indexOf(this.searchWord) > 0;
            }
          });
        } else {
          list = list.filter((i) => {
            if (this.searchFuzzy) {
              return this.searchWordReg(this.searchWord).test(i.Text);
            } else {
              return i.Text.indexOf(this.searchWord) > 0;
            }
          });
        }
      } else if (this.searchTransFlag) {
        if (this.project.spec === "INW") {
          list = list.filter((i) => {
            if (this.searchFuzzy) {
              return this.searchWordReg(this.searchWordTrans).test(
                i.Translated.concat(i.Translated2)
              );
            } else {
              return (
                i.Translated.concat(i.Translated2).indexOf(
                  this.searchWordTrans
                ) > 0
              );
            }
          });
        } else {
          list = list.filter((i) => {
            if (this.searchFuzzy) {
              return this.searchWordReg(this.searchWordTrans).test(
                i.Translated
              );
            } else {
              return i.Translated.indexOf(this.searchWordTrans) > 0;
            }
          });
        }
      }

      return list;
    },
    pageItem() {
      return this.displayList.slice(
        (this.page - 1) * this.size,
        (this.page - 1) * this.size + this.size
      );
    },
    pageLength() {
      return Math.ceil(this.displayList.length / this.size);
    },
    getTalkerName() {
      return function (item) {
        var talker;
        if (this.project.spec === "TKD") {
          talker = this.talkerNameMap.find((i) => i.id === item.EntryO);
          return talker ? talker.name : item.EntryF;
        } else if (this.project.spec === "RAW") {
          talker = this.talkerNameMap.find((i) => i.id === item.EntryK);
          return talker ? talker.name : item.EntryE;
        }
      };
    },
  },
  mounted() {
    this.$store.commit("setCanGoBack", true);
  },
  methods: {
    quickJump(event) {
      if (event.key === "Enter") {
        if (this.jump >= this.project.total) {
          this.alert = true;
          this.alertMessage = "";
          return;
        }
        for (var i = 0; i < this.displayList.length; i++) {
          if (this.displayList[i].Id == this.jump) {
            this.page = Math.ceil((+i + 1) / this.size);
            this.$nextTick(() => {
              this.$vuetify.goTo("#item-" + this.jump);
            });
            return;
          }
        }
        this.alert = true;
        this.alertMessage = "";
      }
    },
    search(event) {
      if (event.key === "Enter") {
        this.searchFlag = true;
      }
    },
    searchTrans(event) {
      if (event.key === "Enter") {
        this.searchTransFlag = true;
      }
    },

    clearSearch() {
      this.searchFlag = false;
      this.searchTransFlag = false;
    },

    sizeChange(newSize) {
      this.size = newSize;
    },

    onTranslatedBlur(event, item, second) {
      if (second) {
        item.Translated2 = event.target.value;
      } else {
        item.Translated = event.target.value;
      }
    },

    loadProject() {
      var _this = this;
      const fileUpload = document.querySelector("#file-input");
      fileUpload.addEventListener("change", () => {
        var files = fileUpload.files;

        if (files.length === 0) {
          this.alertMessage = "";
          this.alert = true;
        } else if (files.length > 1) {
          this.alertMessage = "";
          this.alert = true;
        } else {
          var file = files[0];
          this.filename = file.name;
          var fileReader = new FileReader();
          fileReader.onload = (event) => {
            var data = event.target.result;
            if (data) {
              var prj = JSON.parse(data);
              if (!prj) {
                _this.alertMessage = "";
                _this.alert = true;
                return;
              }
              if (!utils.checkProject(prj.spec)) {
                _this.alertMessage = "";
                _this.alert = true;
                return;
              }
              _this.project = prj;
              _this.checkMode(_this.project.spec);
              _this.page = 1;
              _this.loaded = true;
            }
          };
          fileReader.readAsText(file, "utf-8");
        }
      });
      fileUpload.click();
    },

    checkMode(spec) {
      if (spec === "FMG") {
        this.$store.commit("setCurrentMode", "kuon");
      } else {
        this.$store.commit("setCurrentMode", "nier");
      }
    },

    saveProject() {
      var newProject = this.project;
      newProject.translated = this.progress;
      var timeStr = dateFormat("yyyy-mm-dd_hh-MM-ss");
      FileSaver.saveAs(
        utils.objectToBlob(newProject),
        this.filename.replace(".json", "") + "-" + timeStr + ".json"
      );
    },

    googleclick: function (id) {
      var text = encodeURI(this.project.list[id].Text);
      var url =
        "https://translate.google.cn/?hl=zh-CN#view=home&op=translate&sl=ja&tl=zh-CN&text=";
      window.open(url + text);
    },
    baiduclick: function (id) {
      var text = encodeURI(this.project.list[id].Text);
      var url = "https://fanyi.baidu.com/#jp/zh/";
      window.open(url + text);
    },

    searchWordReg(val) {
      return new RegExp(`(.*)(${val.split("").join(")(.*)(")})(.*)`, "i");
    },
  },
};
</script>