<template>
  <v-container>
    <v-row>
      <v-col cols="3">
        <v-card height="150px">
          <v-card-title></v-card-title>
          <v-card-text>
            <template v-if="network">
              <v-icon small color="green">mdi-circle</v-icon>
              <span class="ml-2 text-justify"></span>
            </template>
            <template v-else>
              <v-icon small color="red">mdi-circle</v-icon>
              <span class="ml-2 text-justify"></span>
            </template>
            <br />
            <span class="mt-2 text-justify"
              > {{ $config.clientVersion }}</span
            >
            <br />
            <span class="mt-2 text-justify" v-if="network"
              > {{ $config.serverVersion }}</span
            >
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card height="150px" @click="goToTranslate">
          <v-card-title></v-card-title>
          <v-card-text>
            <v-icon large>mdi-translate</v-icon>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card height="150px" @click="goToMerge">
          <v-card-title></v-card-title>
          <v-card-text>
            <v-icon large>mdi-merge</v-icon>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card height="150px" @click="goToExport">
          <v-card-title></v-card-title>
          <v-card-text>
            <v-icon large>mdi-export</v-icon>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="3">
        <v-card height="150px" @click="goToCompare">
          <v-card-title></v-card-title>
          <v-card-text>
            <v-icon large>mdi-merge</v-icon>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card height="150px" @click="goToNewIssue">
          <v-card-title></v-card-title>
          <v-card-text>
            <v-icon large>mdi-frequently-asked-questions</v-icon>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card height="150px" @click="goToIssueList">
          <v-card-title></v-card-title>
          <v-card-text>
            <v-icon large>mdi-bug</v-icon>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="3">
        <v-card height="150px" @click="goToNewCompare">
          <v-card-title></v-card-title>
          <v-card-text>
            <v-icon large>mdi-translate</v-icon>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-if="false">
      <v-col>
        <v-card>
          <v-card-title></v-card-title>
          <v-card-text>
            <v-row>
              <v-col>
                <v-subheader style="color: black; font-size: 24px"
                  >Nier Replicant</v-subheader
                >
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col cols="2">TALK</v-col>
              <v-col cols="10">
                <v-progress-linear
                  bottom
                  color="light-blue"
                  height="20"
                  value="1"
                  striped
                ></v-progress-linear>
              </v-col>
            </v-row>
            <v-row no-gutters class="mt-3">
              <v-col cols="2">RADIO</v-col>
              <v-col cols="10">
                <v-progress-linear
                  bottom
                  color="light-blue"
                  height="20"
                  value="1"
                  striped
                ></v-progress-linear>
              </v-col>
            </v-row>
            <v-row no-gutters class="mt-3">
              <v-col cols="2">EVENT</v-col>
              <v-col cols="10">
                <v-progress-linear
                  bottom
                  color="light-blue"
                  height="20"
                  value="1"
                  striped
                ></v-progress-linear>
              </v-col>
            </v-row>
            <v-row no-gutters class="mt-3">
              <v-col cols="2">INFO</v-col>
              <v-col cols="10">
                <v-progress-linear
                  bottom
                  color="light-blue"
                  height="20"
                  value="1"
                  striped
                ></v-progress-linear>
              </v-col>
            </v-row>
            <v-row no-gutters class="mt-3">
              <v-col cols="2">SYSTEM</v-col>
              <v-col cols="10">
                <v-progress-linear
                  bottom
                  color="light-blue"
                  height="20"
                  value="1"
                  striped
                ></v-progress-linear>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      network: false,
    };
  },

  mounted() {
    this.$store.commit("setCanGoBack", false);

    this.$api.getConfig().then((res) => {
      if (res.code === 0) {
        if (this.$config.clientVersion < res.data.clientVersion) {
          //force update
          window.location.reload();
        }

        this.$config.backupInterval = res.data.backupInterval;
        this.$config.serverVersion = res.data.serverVersion;
      }
    });
    //check online service
    this.$api.checkOnlineService().then((res) => {
      if (res.code === 0) {
        this.network = true;
      }
    });
  },
  methods: {
    goToTranslate() {
      this.$router.push("/trans");
    },

    goToMerge() {
      this.$router.push("/merge");
    },

    goToExport() {
      this.$router.push("/export");
    },

    goToNewIssue() {
      this.$router.push("/issues");
    },

    goToIssueList() {
      this.$router.push("/issueList");
    },

    goToCompare() {
      this.$router.push("/compare");
    },

    goToNewCompare() {
      this.$router.push("/newCompare");
    }
  },
};
</script>