<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title></v-card-title>
          <v-card-text>
            <v-tabs @change="onTabChange">
              <v-tab value="0"></v-tab>
              <v-tab value="1"></v-tab>
              <v-tab value="2"></v-tab>
            </v-tabs>
            <v-list three-line>
              <template v-for="issue in issues">
                <v-list-item :key="issue.id" @click="onIssueItemClick(issue.id)">
                  <v-list-item-icon>
                    <v-icon v-if="issue.priority === '0'" color="blue">mdi-circle</v-icon>
                    <v-icon v-else-if="issue.priority === '1'" color="yellow">mdi-circle</v-icon>
                    <v-icon v-else-if="issue.priority === '2'" color="orange">mdi-circle</v-icon>
                    <v-icon v-else-if="issue.priority === '3'" color="red">mdi-circle</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title v-html="issue.title" />
                    <v-list-item-subtitle v-html="subtitle(issue.content)" />
                  </v-list-item-content>
                  <v-list-item-icon>
                    <v-icon v-if="issue.status === 0" color="red">mdi-close</v-icon>
                    <v-icon v-else-if="issue.status === 1" color="green">mdi-check</v-icon>
                    <v-icon v-else-if="issue.status === 2" color="black">mdi-help</v-icon>
                  </v-list-item-icon>
                </v-list-item>
              </template>
            </v-list>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="alert" width="600px">
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>{{alertMessage}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="red" @click="alert = false; alertMessage ='';"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    loaded: false,
    alert: false,
    alertMessage: "",
    page: 0,
    limit: 10,
    status: 1,
    issues: []
  }),
  computed: {
    subtitle() {
      return function(str) {
        if (str && str.length > 40) {
          return str.substring(0, 40) + "...";
        } else {
          return str;
        }
      };
    }
  },
  mounted() {
    this.$store.commit("setCanGoBack", true);
  },
  methods: {
    loadList() {
      this.$api.issueList(this.page, this.limit, this.status).then(res => {
        if (res.code === 0) {
          this.issues = res.data;
        }
      });
    },
    onTabChange(v) {
      this.status = v;
      this.loadList();
    },
    onIssueItemClick(id) {
      this.$router.push({ name: "IssueDetailPage", params: { id: id } });
    }
  }
};
</script>