<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title></v-card-title>
          <v-card-text>
            <v-file-input label="" @change="fileInputchange"></v-file-input>
            <v-file-input label="" @change="fileInputchange"></v-file-input>
            <v-btn
              class="ml-2"
              color="primary"
              :disabled="projects.length !== 2 || loaded"
              @click="compareProject"
            ></v-btn>
            <v-btn class="ml-2" color="success" :disabled="!canSave" @click="saveProject"></v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row v-if="loaded && compareListItems.length !== 0" id="work-row">
      <v-col>
        <v-card>
          <v-card-subtitle> {{compareListItems.length}} </v-card-subtitle>
          <v-card-text>
            <v-btn text @click="comfirmAll()">B</v-btn>
            <v-pagination v-model="page" :length="pageLength" total-visible="10" circle></v-pagination>
            <template>
              <div v-for="item in pageItem" :key="item.Id">
                <v-row no-gutters dense>
                  <v-col cols="4">
                    <span>A:#{{item.AId}}</span>
                  </v-col>
                  <v-col cols="4">
                    <span>B:#{{item.BId}}</span>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="2">
                    <v-textarea label="" readonly no-resize :value="item.AText"></v-textarea>
                  </v-col>
                  <v-col cols="2">
                    <v-textarea label="A" readonly no-resize :value="item.ATranslated"></v-textarea>
                  </v-col>
                  <v-col cols="2">
                    <v-textarea label="B" readonly no-resize :value="item.BText"></v-textarea>
                  </v-col>
                  <v-col cols="2">
                    <v-textarea label="B" readonly no-resize :value="item.BTranslated"></v-textarea>
                  </v-col>
                  <v-col cols="3">
                    <v-textarea label="" no-resize v-model="item.CTranslated"></v-textarea>
                  </v-col>
                </v-row>
                <v-row dense>
                  <v-col cols="2" />
                  <v-col cols="2">
                    <v-btn text @click="comfirmChange('A' ,item)"> A</v-btn>
                  </v-col>
                  <v-col cols="2" />
                  <v-col cols="2">
                    <v-btn text @click="comfirmChange('B' ,item)"> B</v-btn>
                  </v-col>
                  <v-col cols="3">
                    <v-btn text @click="comfirmChange('C' ,item)"> </v-btn>
                  </v-col>
                </v-row>
              </div>
            </template>
            <v-pagination
              v-model="page"
              :length="pageLength"
              total-visible="10"
              circle
              @input="$vuetify.goTo('#work-row')"
            ></v-pagination>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-dialog v-model="alert" width="600px">
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>{{alertMessage}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="red" @click="alert = false; alertMessage ='';"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import utils from "../utils/utils";
import FileSaver from "file-saver";
export default {
  data: () => ({
    loaded: false,
    alert: false,
    alertMessage: "",
    projects: [],
    compareListItems: [],
    originalFilename: "",
    page: 1,
    size: 50,
  }),
  computed: {
    canSave() {
      if (!this.loaded) {
        return false;
      }
      return true;
    },
    pageItem() {
      return this.compareListItems.slice(
        (this.page - 1) * this.size,
        (this.page - 1) * this.size + this.size
      );
    },
    pageLength() {
      return Math.ceil(this.compareListItems.length / this.size);
    },
  },
  mounted() {
    this.$store.commit("setCanGoBack", true);
  },
  methods: {
    fileInputchange(file) {
      if (file !== "") {
        var _this = this;
        var fileReader = new FileReader();
        fileReader.onload = (event) => {
          var data = event.target.result;
          if (data) {
            var prj = JSON.parse(data);
            if (!prj) {
              _this.alertMessage = "";
              _this.alert = true;
              return;
            }
            if (!utils.checkProject(prj.spec)) {
              _this.alertMessage = "";
              _this.alert = true;
              return;
            }
            if (_this.projects.length === 0) {
              _this.originalFilename = file.name;
            }
            _this.projects.push(prj);
          }
        };
        fileReader.readAsText(file, "utf-8");
      }
    },

    compareProject() {
      var projectA = this.projects[0];

      if (projectA.spec === "INW") {
        this.alert = true;
        this.alertMessage = "INFO";
      }

      var projectB = this.projects[1];
      var AList = projectA.list;
      var BList = projectB.list;

      for (var AIdx in AList) {
        let txt = AList[AIdx].Text;
        // B
        for (var BIdx in BList) {
          if (txt === BList[BIdx].Text) {
            this.compareListItems.push({
              AId: AList[AIdx].Id,
              AText: AList[AIdx].Text,
              ATranslated: AList[AIdx].Translated,
              BId: BList[BIdx].Id,
              BText: BList[BIdx].Text,
              BTranslated: BList[BIdx].Translated,
            });
            break;
          }
        }
      }
      this.loaded = true;
    },

    comfirmChange(which, item) {
      if (which === "A") {
        // 
      } else if (which === "B") {
        this.projects[0].list[item.AId].Translated = item.BTranslated;
      } else if (which === "C") {
        this.projects[0].list[item.AId].Translated = item.CTranslated;
      } else {
        this.alert = true;
        this.alertMessage = "";
      }
      this.compareListItems.splice(this.compareListItems.indexOf(item), 1);
    },

    comfirmAll() {
      for (var idx in this.compareListItems) {
        let item = this.compareListItems[idx];
        this.projects[0].list[item.AId].Translated = item.BTranslated;
      }
      this.compareListItems = [];
    },

    saveProject() {
      FileSaver.saveAs(
        utils.objectToBlob(this.projects[0]),
        this.originalFilename
      );
    },
  },
};
</script>