export default {
    arrayEqual(a, b) {
        if (a.length !== b.length) {
            return false;
        } else {
            for (let i = 0; i < a.length; i++) {
                if (a[i] !== b[i]) {
                    return false;
                }
            }
            return true;
        }
    },
    checkProject(spec) {
        const knownSpecs = ["TKD", "TXD", "RAW", "INW", "CAP", "FMG", "V122_TALK"];
        return knownSpecs.includes(spec);
    },
    objectToBlob(obj) {
        var json = JSON.stringify(obj);
  
        var blob = new Blob([json], { type: "application/json;charset=utf-8" });
        return blob;
      },
}