<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title></v-card-title>
          <v-card-text>
            <v-form ref="form" v-model="valid">
              <v-text-field v-model="issueTitle" label="" required :rules="[v => !!v || '']" />
              <v-select label="" v-model="issueTag" :items="tagItems" />
              <v-row>
                <v-col>
                  <v-select v-model="issueLine" :items="lineItems" label="" />
                </v-col>
                <v-col>
                  <v-text-field v-model="issueIndex" label="" :rules="[v => !!v || '']" />
                </v-col>
              </v-row>
              <v-textarea v-model="issueContent" label="" required :rules="[v => !!v || '']" />
              <v-select label="" v-model="issuePriority" :items="priorityItems" />
              <v-file-input
                placeholder=""
                multiple
                chips
                label=""
                accept="image/png, image/jpeg, image/bmp, image/gif, image/webp"
                v-model="issueFiles"
              ></v-file-input>
            </v-form>

            <v-btn color="success" :disabled="!valid" @click="onSubmitClick()"></v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="alert" width="600px">
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>{{alertMessage}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="red" @click="alert = false; alertMessage ='';"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    loaded: false,
    alert: false,
    alertMessage: "",
    valid: true,
    tagItems: [
      { text: "", value: "nier" },
      { text: "", value: "kuon" }
    ],
    priorityItems: [
      { text: "", value: "0" },
      { text: "", value: "1" },
      { text: "", value: "2" },
      { text: "", value: "3" }
    ],
    lineItems: [
      { text: "TALK_A", value: "TA" },
      { text: "TALK_B", value: "TB" },
      { text: "TALK_C", value: "TC" },
      { text: "TALK_COMMON", value: "TCOMM" },
      { text: "TALK_D", value: "TD" },
      { text: "RADIO_A", value: "RA" },
      { text: "RADIO_B", value: "RB" },
      { text: "INFOWINDOW", value: "INFO" },
      { text: "CAPTION", value: "CAP" },
      { text: "EVENT", value: "EVENT" },
      { text: "FMG", value: "FMG" }
    ],
    issueTitle: "",
    issueLine: "TA",
    issueIndex: "",
    issueContent: "",
    issueTag: "nier",
    issuePriority: '1', // 0 1 2 3
    issueFiles: []
  }),
  mounted() {
    this.$store.commit("setCanGoBack", true);
  },
  methods: {
    resetForm() {
      this.issueTitle = "";
      this.issueLine = "TA";
      this.issueIndex = "";
      this.issueContent = "";
      this.issueTag = "nier";
      this.issuePriority = "1";
      this.issueFiles = [];
    },
    onSubmitClick() {
      this.$api
        .createIssue({
          title: this.issueTitle,
          tag: this.issueTag,
          line: this.issueLine,
          index: this.issueIndex,
          priority: this.issuePriority,
          content: this.issueContent,
          files: this.issueFiles
        })
        .then(res => {
          if (res.code === 0) {
            this.alertMessage = "";
            this.alert = true;
            this.resetForm();
          } else {
            this.alertMessage = "";
            this.alert = true;
          }
        })
        .catch(() => {
          this.alertMessage = "";
          this.alert = true;
        });
    }
  }
};
</script>