import axios from 'axios'
import qs from 'qs'

const check_online_service_url = '/api/common/check'
const config_url = '/api/common/config'
const backup_url = '/api/nier/backup'
const export_url = '/api/nier/export'
const create_issue_url = "/api/issue/create"
const issues_url = "/api/issue/list"
const get_issue_url = "/api/issue/get"
const update_issue_url = "/api/issue/update"

axios.withCredentials = true;

axios.interceptors.request.use(
    config => {
        if (config.headers['Content-Type'] === 'application/x-www-form-urlencoded') {
            config.data = qs.stringify(config.data);
            config.headers = {
                'Content-Type': 'application/x-www-form-urlencoded'
            }
            return config;
        }

        if (config.headers['Content-Type'] !== 'multipart/form-data') {
            config.data = JSON.stringify(config.data);
            config.headers = {
                'Content-Type': 'application/json'
            }
        }
        return config;

    },
    error => {
        return Promise.reject(error);
    }
)

axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        return Promise.reject(error);
    }
)

export default {
    checkOnlineService() {
        return new Promise((resolve, reject) => {
            axios.get(check_online_service_url)
                .then(response => resolve(response.data))
                .catch(err => reject(err))
        })
    },

    getConfig() {
        return new Promise((resolve, reject) => {
            axios.get(config_url)
                .then(response => resolve(response.data))
                .catch(err => reject(err))
        })
    },

    backup(file) {
        var formData = new FormData();
        formData.append('file', file);

        return new Promise((resolve, reject) => {
            axios.post(backup_url, formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(response => resolve(response.data))
                .catch(err => reject(err))
        })

    },

    export (file, data) {
        var formData = new FormData();
        formData.append('file', file);
        formData.append("debug", data.debug);
        formData.append("english", data.english)
        formData.append('lang', data.lang);

        return new Promise((resolve, reject) => {
            axios.post(export_url, formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(response => resolve(response))
                .catch(err => reject(err))
        })

    },

    issueList(page, limit, status) {
        return new Promise((resolve, reject) => {
            axios.get(issues_url, {
                    params: {
                        page: page,
                        limit: limit,
                        status: status
                    }
                })
                .then(response => resolve(response.data))
                .catch(err => reject(err))
        })
    },

    getIssue(id) {
        return new Promise((resolve, reject) => {
            axios.get(get_issue_url, {
                    params: {
                        id: id
                    }
                })
                .then(response => resolve(response.data))
                .catch(err => reject(err))
        })
    },

    createIssue(issueObj) {
        var formData = new FormData();
        formData.append('title', issueObj.title)
        formData.append('tag', issueObj.tag)
        formData.append('line', issueObj.line)
        formData.append('index', issueObj.index)
        formData.append('content', issueObj.content)
        formData.append('priority', issueObj.priority)
        issueObj.files.forEach(file => formData.append('files', file))


        return new Promise((resolve, reject) => {
            axios.post(create_issue_url, formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(response => resolve(response.data))
                .catch(err => reject(err))
        })
    },

    updateIssue(id, priority, status, comment) {
        return new Promise((resolve, reject) => {
            axios.post(update_issue_url, {
                    id: id,
                    priority: priority,
                    status: status,
                    comment: comment
                }, {
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then(response => resolve(response.data))
                .catch(err => reject(err))
        })
    }
}