<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title></v-card-title>
          <v-card-text>
            <v-file-input label="A" @change="fileInputchange"></v-file-input>
            <v-file-input label="B" @change="fileInputchange"></v-file-input>
            <v-btn
              class="ml-2"
              color="primary"
              :disabled="projects.length !== 2 || loaded"
              @click="compareProject"
            ></v-btn>
            <v-btn class="ml-2" color="success" :disabled="!canSave" @click="saveProject"></v-btn>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-row v-if="loaded && diffItemIdxs.length !== 0" id="work-row">
      <v-col>
        <v-card>
          <v-card-subtitle></v-card-subtitle>
          <v-card-text>
            <v-btn text @click="comfirmAll('left')">A</v-btn>
            <v-btn text @click="comfirmAll('right')">B</v-btn>
            <v-pagination v-model="page" :length="pageLength" total-visible="10" circle></v-pagination>
            <template v-if="newProject.spec === 'INW'">
              <div v-for="(id, index) in pageItem" :key="id">
                <v-row no-gutters dense>
                  <v-col>
                    <span>#{{id}}</span>
                  </v-col>
                </v-row>
                <v-row no-gutters>
                  <v-col cols="1">
                    <v-textarea label="1" readonly no-resize :value="projects[0].list[id].Text"></v-textarea>
                  </v-col>
                  <v-col cols="1">
                    <v-textarea label="2" readonly no-resize :value="projects[0].list[id].Text2"></v-textarea>
                  </v-col>
                  <v-col cols="2">
                    <v-textarea
                      label="A 1"
                      readonly
                      no-resize
                      :value="projects[0].list[id].Translated"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="2">
                    <v-textarea
                      label="A 2"
                      readonly
                      no-resize
                      :value="projects[0].list[id].Translated2"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="1" class="ma-2">
                    <div>
                      <v-btn text @click="comfirmChange('left' ,id, index)"> A</v-btn>
                      <v-btn text @click="comfirmChange('right',id, index)">B </v-btn>
                    </div>
                  </v-col>
                  <v-col cols="2" class="ml-2">
                    <v-textarea
                      label="B 1"
                      readonly
                      no-resize
                      :value="projects[1].list[id].Translated"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="2" class="ml-2">
                    <v-textarea
                      label="B 2"
                      readonly
                      no-resize
                      :value="projects[1].list[id].Translated2"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </div>
            </template>
            <template v-else>
              <div v-for="(id, index) in pageItem" :key="id">
                <v-row no-gutters dense>
                  <v-col>
                    <span>#{{id}}</span>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col cols="3">
                    <v-textarea label="" readonly no-resize :value="projects[0].list[id].Text"></v-textarea>
                  </v-col>
                  <v-col cols="3">
                    <v-textarea
                      label="A"
                      readonly
                      no-resize
                      :value="projects[0].list[id].Translated"
                    ></v-textarea>
                  </v-col>
                  <v-col cols="1" class="ma-2">
                    <div>
                      <v-btn text @click="comfirmChange('left' ,id, index)"> A</v-btn>
                      <v-btn text @click="comfirmChange('right',id, index)">B </v-btn>
                    </div>
                  </v-col>
                  <v-col cols="3" class="ml-2">
                    <v-textarea
                      label="B"
                      readonly
                      no-resize
                      :value="projects[1].list[id].Translated"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </div>
            </template>
            <v-pagination
              v-model="page"
              :length="pageLength"
              total-visible="10"
              circle
              @input="$vuetify.goTo('#work-row')"
            ></v-pagination>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="alert" width="600px">
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>{{alertMessage}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="red" @click="alert = false; alertMessage ='';"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import utils from "../utils/utils";
import FileSaver from "file-saver";
import dateFormat from "dateformat";
export default {
  data: () => ({
    loaded: false,
    alert: false,
    alertMessage: "",
    projects: [],
    diffItemIdxs: [],
    newProject: {},
    page: 1,
    size: 50
  }),
  computed: {
    canSave() {
      if (!this.loaded) {
        return false;
      }
      if (this.diffItemIdxs.length !== 0) {
        return false;
      }
      return true;
    },
    pageItem() {
      return this.diffItemIdxs.slice(
        (this.page - 1) * this.size,
        (this.page - 1) * this.size + this.size
      );
    },
    pageLength() {
      return Math.ceil(this.diffItemIdxs.length / this.size);
    }
  },
  mounted() {
    this.$store.commit("setCanGoBack", true);
  },
  methods: {
    fileInputchange(file) {
      if (file !== "") {
        var _this = this;
        var fileReader = new FileReader();
        fileReader.onload = event => {
          var data = event.target.result;
          if (data) {
            var prj = JSON.parse(data);
            if (!prj) {
              _this.alertMessage = "";
              _this.alert = true;
              return;
            }
            if (!utils.checkProject(prj.spec)) {
              _this.alertMessage = "";
              _this.alert = true;
              return;
            }
            _this.projects.push(prj);
          }
        };
        fileReader.readAsText(file, "utf-8");
      }
    },

    compareProject() {
      var projectA = this.projects[0];
      var projectB = this.projects[1];
      var projectAKeys = Object.keys(projectA.list[0]);
      var projectBKeys = Object.keys(projectB.list[0]);

      if (projectA.total !== projectB.total) {
        this.alert = true;
        this.alertMessage = "";
        this.projects = [];
      }

      if (utils.arrayEqual(projectAKeys, projectBKeys)) {
        this.newProject = projectA;

        for (let i = 0; i < projectA.list.length; i++) {
          if (projectA.list[i].Translated !== projectB.list[i].Translated) {
            if (projectA.list[i].Translated === "") {
              this.newProject.list[i].Translated = projectB.list[i].Translated;
            } else if (projectB.list[i].Translated === "") {
              //
            } else {
              this.diffItemIdxs.push(i);
            }
          }
        }

        if (this.newProject.spec === "INW") {
          for (let i = 0; i < projectA.list.length; i++) {
            if (projectA.list[i].Translated2 !== projectB.list[i].Translated2) {
              if (projectA.list[i].Translated2 === "") {
                this.newProject.list[i].Translated2 =
                  projectB.list[i].Translated2;
              } else if (projectB.list[i].Translated2 === "") {
                //
              } else {
                this.diffItemIdxs.push(i);
              }
            }
          }
        }

        this.loaded = true;
      } else {
        this.alert = true;
        this.alertMessage = "";
        this.projects = [];
      }
    },

    comfirmChange(which, id, index) {
      if (which === "left") {
        this.newProject.list[id].Translated = this.projects[0].list[
          id
        ].Translated;
        if (this.newProject.spec === "INW") {
          this.newProject.list[id].Translated2 = this.projects[0].list[
            id
          ].Translated2;
        }
        this.diffItemIdxs.splice(index, 1);
      } else if (which === "right") {
        this.newProject.list[id].Translated = this.projects[1].list[
          id
        ].Translated;
        if (this.newProject.spec === "INW") {
          this.newProject.list[id].Translated2 = this.projects[1].list[
            id
          ].Translated2;
        }
        this.diffItemIdxs.splice(index, 1);
      } else {
        this.alert = true;
        this.alertMessage = "";
      }
    },

    comfirmAll(which) {
      if (which === "left") {
        for (let i = 0; i < this.diffItemIdxs.length; i++) {
          this.newProject.list[
            this.diffItemIdxs[i]
          ].Translated = this.projects[0].list[this.diffItemIdxs[i]].Translated;
        }
        if (this.newProject.spec === "INW") {
          for (let i = 0; i < this.diffItemIdxs.length; i++) {
            this.newProject.list[
              this.diffItemIdxs[i]
            ].Translated2 = this.projects[0].list[
              this.diffItemIdxs[i]
            ].Translated2;
          }
        }
        this.diffItemIdxs = [];
      } else if (which === "right") {
        for (let i = 0; i < this.diffItemIdxs.length; i++) {
          this.newProject.list[
            this.diffItemIdxs[i]
          ].Translated = this.projects[1].list[this.diffItemIdxs[i]].Translated;
        }
        if (this.newProject.spec === "INW") {
          for (let i = 0; i < this.diffItemIdxs.length; i++) {
            this.newProject.list[
              this.diffItemIdxs[i]
            ].Translated2 = this.projects[1].list[
              this.diffItemIdxs[i]
            ].Translated2;
          }
        }
        this.diffItemIdxs = [];
      } else {
        this.alert = true;
        this.alertMessage = "";
      }
    },

    saveProject() {
      var translated = 0;
      this.newProject.list.forEach(element => {
        "" !== element.Translated &&
          null !== element.Translated &&
          translated++;
      });
      this.newProject.translated = translated;
      var timeStr = dateFormat("yyyy-mm-dd_hh-MM-ss");
      FileSaver.saveAs(
        utils.objectToBlob(this.newProject),
        timeStr + "-merged-" + ".json"
      );
    }
  }
};
</script>