<template>
  <v-container>
    <v-row>
      <v-col>
        <v-card>
          <v-card-title>
            {{issue.title}}
            <v-chip
              class="ml-4"
              :color="getTag(issue.tag).color"
              text-color="white"
            >{{getTag(issue.tag).text}}</v-chip>
          </v-card-title>
          <v-card-text>{{issue.content}}</v-card-text>
          <v-card-text>
            <v-row>
              <v-col cols="3">
                <v-label></v-label>
                <v-label>{{getLine(issue.line).text}}</v-label>
              </v-col>
              <v-col cols="3">
                <v-label></v-label>
                <v-label>{{issue.project_index}}</v-label>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="3">
                <v-label></v-label>
                <v-chip
                  :color="getPriority(issue.priority).color"
                  text-color="white"
                >{{getPriority(issue.priority).text}}</v-chip>
              </v-col>
              <v-col cols="3">
                <v-label></v-label>
                <v-chip>
                  <v-icon left>{{getStatus(issue.status).icon}}</v-icon>
                  {{getStatus(issue.status).text}}
                </v-chip>
              </v-col>
              <v-col>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="edit = true"></v-btn>
              </v-col>
            </v-row>
            <v-row v-if="issue.comment">
              <v-card-text class="txt-subtitle"></v-card-text>
              <v-spacer></v-spacer>
              <v-card-text>{{issue.comment}}</v-card-text>
            </v-row>
          </v-card-text>

          <v-card-text v-if="issue.files" class="txt-subtitle-image"></v-card-text>
          <template v-for="img in issue.files">
            <v-img class="mb-2" :key="img" :src="imageUrl(img)" />
          </template>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="alert" width="600px">
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>{{alertMessage}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="red" @click="alert = false; alertMessage ='';"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="edit" width="600px" persistent>
      <v-card>
        <v-card-title class="subtitle"></v-card-title>
        <v-card-text>
          <v-form>
            <v-select label="" v-model="issueNewPriority" :items="priorityItems" />
            <v-select label="" v-model="issueNewStatus" :items="statusItems" />
            <v-textarea v-model="issueNewComment" label="" />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="edit = false"></v-btn>
          <v-btn text color="green" @click="onUpdateClick()"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    loaded: false,
    alert: false,
    alertMessage: "",
    priorityItems: [
      { text: "", value: "0", color: "blue" },
      { text: "", value: "1", color: "yellow" },
      { text: "", value: "2", color: "orange" },
      { text: "", value: "3", color: "red" }
    ],
    statusItems: [
      { text: "", value: 0, icon: "mdi-close" },
      { text: "", value: 1, icon: "mdi-check" },
      { text: "", value: 2, icon: "mdi-help" }
    ],
    tagItems: [
      { text: "", value: "nier", color: "blue" },
      { text: "", value: "kuon", color: "red" }
    ],
    lineItems: [
      { text: "TALK_A", value: "TA" },
      { text: "TALK_B", value: "TB" },
      { text: "TALK_C", value: "TC" },
      { text: "TALK_COMMON", value: "TCOMM" },
      { text: "TALK_D", value: "TD" },
      { text: "RADIO_A", value: "RA" },
      { text: "RADIO_B", value: "RB" },
      { text: "INFOWINDOW", value: "INFO" },
      { text: "CAPTION", value: "CAP" },
      { text: "EVENT", value: "EVENT" },
      { text: "FMG", value: "FMG" }
    ],
    issue: "",
    edit: false,
    issueNewPriority: 0,
    issueNewStatus: 0,
    issueNewComment: ""
  }),
  computed: {
    imageUrl() {
      return function(img) {
        if (img) {
          return "/file/image/" + img;
        }
      };
    },
    getPriority() {
      return function(p) {
        var item = this.priorityItems.find(item => {
          return item.value === p;
        });
        return item;
      };
    },
    getStatus() {
      return function(status) {
        var item = this.statusItems.find(item => {
          return item.value === status;
        });
        return item;
      };
    },
    getTag() {
      return function(tag) {
        var item = this.tagItems.find(item => {
          return item.value === tag;
        });
        return item;
      };
    },
    getLine() {
      return function(line) {
        var item = this.lineItems.find(item => {
          return item.value === line;
        });
        return item;
      };
    }
  },
  created() {
    this.init();
  },
  mounted() {
    this.$store.commit("setCanGoBack", true);
  },
  methods: {
    init() {
      this.$api.getIssue(this.$route.params.id).then(res => {
        if (res.code === 0) {
          this.issue = res.data;
          if (this.issue.files) {
            this.issue.files = this.issue.files.split(",");
          }
          this.issueNewPriority = this.issue.priority;
          this.issueNewStatus = this.issue.status;
        }
      });
    },
    onUpdateClick() {
      this.$api
        .updateIssue(
          this.issue.id,
          this.issueNewPriority,
          this.issueNewStatus,
          this.issueNewComment
        )
        .then(res => {
          if (res.code === 0) {
            this.alertMessage = "";
            this.alert = true;
            this.edit = false;
            this.issue.priority = this.issueNewPriority;
            this.issue.status = this.issueNewStatus;
            this.issue.comment = this.issueNewComment;
          } else {
            this.alertMessage = "";
            this.alert = true;
            this.edit = false;
          }
        })
        .catch(() => {
          this.alertMessage = "";
          this.alert = true;
          this.edit = false;
        });
    }
  }
};
</script>
<style scoped>
.txt-subtitle {
  font-weight: bold;
  padding: 0px;
  padding-left: 16px;
}
.txt-subtitle-image {
  font-weight: bold;
  padding: 0px;
  padding-left: 16px;
  padding-bottom: 16px;
}
</style>