import Vue from 'vue'
import VueRouter from 'vue-router'

import MainPage from '../views/MainPage'
import TranslatePage from '../views/TranslatePage'
import MergePage from '../views/MergePage'
import ExportPage from '../views/ExportPage'
import IssuesPage from '../views/IssuesPage'
import IssueListPage from '../views/IssueListPage'
import IssueDetailPage from '../views/IssueDetailPage'
import ComparePage from '../views/ComparePage'
import NewComparePage from '../views/NewComparePage'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'MainPage',
    component: MainPage
  },
  {
    path: '/trans',
    name: 'TranslatePage',
    component: TranslatePage
  },
  {
    path: '/merge',
    name: 'MergePage',
    component: MergePage
  },
  {
    path: '/export',
    name: 'ExportPage',
    component: ExportPage
  },
  {
    path: '/issues',
    name: 'IssuesPage',
    component: IssuesPage
  },
  {
    path: '/issueList',
    name: 'IssueListPage',
    component: IssueListPage
  },
  {
    path: '/issueDetail',
    name: 'IssueDetailPage',
    component: IssueDetailPage
  },
  {
    path: '/compare',
    name: 'ComparePage',
    component: ComparePage
  },
  {
    path: '/newCompare',
    name: 'NewComparePage',
    component: NewComparePage
  }
]

const router = new VueRouter({
  routes
})

export default router
